  outline: none !important;
  border: none;
  background: none;
}

button {
  cursor: pointer;
}

a,
a:hover,
a:focus,
a:active {
  text-decoration: none !important;
  cursor: pointer;
  color: #000;
}

ul {
  margin-bottom: 0;
}

.nav-link a,
.service-card,
.bg-title,
.arrow-action,
.offer-box,
.icon-left-arrow,
.icon-right-arrow,
.vector-hover,
.menu-bar > div {
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.center-vertically {
  vertical-align: middle;
  overflow-y: auto;
}
.center-vertically:before {
  content: "";
  vertical-align: middle;
  margin-right: -4px;
}
.center-vertically > div {
  vertical-align: middle;
}
.cen-ver-position {
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
.width-fluid {
  width: 100% !important;
}
.width-fluid > .height-fluid {
  height: 100%;
}
.form-label {
  font-size: 20px;
  font-weight: 500;
  color: #0f1c49;
  display: block;
}
.form-control {
  display: block;
  color: #000;
  font-size: 16px;
  background: #fffbfa;
  font-weight: 400;
  height: auto;
  border-radius: 8px;
  border: 1px solid #28294b1a;
  width: 100%;
  padding: 10px 15px;
  outline: none;
  resize: none;
  line-height: 1;
}
.form-control:focus {
  box-shadow: none;
}
.form-control::-webkit-input-placeholder {
  color: #28294b60;
  font-size: 15px;
  font-weight: 400;
  line-height: 1;
}
.form-control:-ms-input-placeholder {
  color: #28294b60;
  font-size: 15px;
  font-weight: 400;
  line-height: 1;
}
.form-control::-ms-input-placeholder {
  color: #28294b60;
  font-size: 15px;
  font-weight: 400;
  line-height: 1;
}
.form-control::placeholder {
  color: #28294b60;
  font-size: 15px;
  font-weight: 400;
  line-height: 1;
}
.form-control:hover {
  outline: none;
}
.form-control:active {
  outline: none;
}
.form-control:focus {
  outline: none;
  background: #fffbfa;
  border: 1px solid #28294b1a;
}

input[type="file"] {
  opacity: 0;
  z-index: 20;
  top: 0;
  left: 0;
}

.circle {
  border-radius: 100% !important;
}

.divider {
  height: 1px;
  width: 100%;
  background-color: #333;
}
.divider .divider-text {
  background: #fff;
  color: #808080;
  display: inline-block;
  padding: 3px 5px;
  top: 50%;
  left: 50%;
  font-size: 14px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.title {
  font-weight: 600;
  font-size: 22px;
}

.container-fluid {
  padding-left: 50px;
  padding-right: 50px;
}

small {
  color: #333;
  font-weight: 600;
}

.divider {
  height: 0.5px;
  background-color: #989898;
}

.main-border {
  border: 1px solid #333333;
}

.gradient-easing {
  background-image: -webkit-gradient(
    linear,
    left top, left bottom,
    from(rgba(0, 0, 0, 0)),
    color-stop(10.47%, rgba(0, 0, 0, 0.00709)),
    color-stop(19.56%, rgba(0, 0, 0, 0.02755)),
    color-stop(27.58%, rgba(0, 0, 0, 0.06016)),
    color-stop(34.81%, rgba(0, 0, 0, 0.1037)),
    color-stop(41.57%, rgba(0, 0, 0, 0.15697)),
    color-stop(48.13%, rgba(0, 0, 0, 0.21875)),
    color-stop(54.79%, rgba(0, 0, 0, 0.28782)),
    color-stop(61.85%, rgba(0, 0, 0, 0.36296)),
    color-stop(69.61%, rgba(0, 0, 0, 0.44297)),
    color-stop(78.36%, rgba(0, 0, 0, 0.52662)),
    color-stop(88.39%, rgba(0, 0, 0, 0.6127)),
    to(rgba(0, 0, 0, 0.7))
  );
  background-image: linear-gradient(
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0.00709) 10.47%,
    rgba(0, 0, 0, 0.02755) 19.56%,
    rgba(0, 0, 0, 0.06016) 27.58%,
    rgba(0, 0, 0, 0.1037) 34.81%,
    rgba(0, 0, 0, 0.15697) 41.57%,
    rgba(0, 0, 0, 0.21875) 48.13%,
    rgba(0, 0, 0, 0.28782) 54.79%,
    rgba(0, 0, 0, 0.36296) 61.85%,
    rgba(0, 0, 0, 0.44297) 69.61%,
    rgba(0, 0, 0, 0.52662) 78.36%,
    rgba(0, 0, 0, 0.6127) 88.39%,
    rgba(0, 0, 0, 0.7)
  );
  bottom: 0;
  right: 0;
  left: 0;
  height: 370px;
}
.center-v-h {
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.alert-warning {
  background: none;
  margin-bottom: 15px;
}
.alert-warning .alert-text {
  font-weight: 600;
  font-size: 17px;
}
.alert-warning .alert-text,
.alert-warning .icon {
  color: #cc2631;
}
.alert-warning .icon {
  margin-left: 10px;
  font-size: 28px;
}
.corner {
  border-radius: 50px !important;
}
.icon-right-arrow,
.icon-left-arrow {
  color: #4bc1bc;
  font-weight: 600;
}
.page-header {
  padding: 40px 15px;
  background: #4bc1bc;
}
.page-header .bg-title {
  color: #fff;
  font-size: 43px;
}
/** Buttons **/
.main-btn {
  background: #2f318d19;
  color: #2f318d !important;
}
.main-btn:hover {
  background-color: #2f318d2e;
  color: #2f318d !important;
}

.main-btn:active {
  opacity: 0.7;
}

.main-btn-gradient {
  background: transparent
    linear-gradient(67deg, #34bcb2 0%, #2cd8ae 56%, #25ce8a 100%) 0% 0%
    no-repeat padding-box;
  color: #fff !important;
}
.main-btn-gradient:hover {
  background: transparent
    linear-gradient(67deg, #34bcb2 30%, #2cd8ae 76%, #25ce8a 90%) 0% 0%
    no-repeat padding-box;
  color: #fff !important;
}
.main-btn-gradient:active {
  opacity: 0.7;
}

.simple-btn {
  display: inline-block;
  color: #28294b;
  font-size: 15px;
  position: relative;
}
.simple-btn:after {
  content: "";
  position: absolute;
  right: -43%;
  width: 20px;
  height: 2px;
  background: #29ec9e;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.btn {
  border-radius: 50px;
  padding: 12px 23px;
  font-size: 14px;
  line-height: 1;
  font-weight: 400;
}
.btn:focus {
  -o-box-shadow: none;
  -ms-box-shadow: none;
  box-shadow: none;
}

/** Buttons **/

.wrapper {
  min-height: 100vh;
  padding-top: 88.21px;
}
.main-title {
  margin-bottom: 25px;
}
.main-title .sm-title {
  color: #4bc1bc;
  font-size: 14px;
  margin-bottom: 10px;
  text-transform: uppercase;
}
.main-title .sub-title {
  font-size: 25px;
}
.main-title .sub-title span {
  font-weight: 600;
}
.header-w-arrows {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
.arrow-action {
  width: 50px;
  height: 50px;
  border: 2px solid #4bc1bc;
  color: #4bc1bc;
  border-radius: 50%;
  margin-left: 10px;
}
.arrow-action:hover {
  background: #4bc1bc;
  color: #fff;
}
.arrow-action:hover .icon-left-arrow,
.arrow-action:hover .icon-right-arrow {
  color: #fff !important;
}
.arrow-action:active {
  background-color: #45bdb8;
  color: #fff;
}
.text {
  color: #28294b;
  font-size: 15px;
  line-height: 1.7;
}
.overlay {
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(15, 28, 73, 1);
  width: 100%;
  height: 100%;
  z-index: 10;
}
.bg-title {
  font-weight: bold;
  font-size: 43px;
  line-height: 1.2;
  color: #2f318d;
}
/* Global */

/* Landing Page */
.header {
  background: #fffbfa;
  padding: 70px 0;
}
.header-items {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
}
.header-details,
.header-view {
  width: 42%;
}

.header-leaders .title {
  font-weight: 500;
  font-size: 15px;
}
.weare-container {
  padding: 100px 0;
}
.weare-view {
  width: 45%;
}

.studies-container {
  padding: 100px 0;
}
.study-card .md-title {
  font-size: 24px;
  font-weight: 500;
  color: #28294b;
}
.study-card img {
  border-radius: 12px;
  height: 200px;
  width: 100%;
}
/* Landing Page */

/* Extra small devices (portrait phones, less than 576px) */
@media (max-width: 575.98px) {
  .simple-btn {
    display: none;
  }
  .bg-title {
    font-size: 33px;
  }
  .header-details,
  .header-view {
    width: 100%;
  }
  .header-view {
    display: none;
  }
  .weare-box {
    text-align: center;
  }
  .weare-box {
    margin-bottom: 30px;
  }
  .study-card {
    margin-bottom: 60px;
  }
  .studies-container div[class*="col-"]:last-of-type .study-card {
    margin-bottom: 0;
  }
  .blog-card {
    margin-bottom: 50px;
  }
  .blogs-container div[class*="col-"]:last-of-type .blog-card {
    margin-bottom: 0;
  }
  .studies-container {
    padding: 20px 0;
  }
}

/* Small devices (landscape phones, 767.98px Max width) */
@media (max-width: 767.98px) {
  .wrapper {
    padding-top: 71.51px;
  }
}

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) and (max-width: 767.98px) {
  .bg-title {
    font-size: 34px;
  }
  .weare-view {
    width: 80%;
  }
  .offer-view {
    width: 75%;
  }
  .step-card {
    width: 26%;
    margin-bottom: 30px;
  }
  .step-card:not(:first-of-type):after {
    content: none;
  }
  .study-card img {
    height: auto;
  }
  .study-card .md-title {
    font-size: 20px;
  }
  .study-card {
    margin-bottom: 40px;
  }
  .pricing-card {
    margin-bottom: 30px;
  }
  .blog-card img {
    height: auto;
  }
  .blog-card {
    margin-bottom: 40px;
  }
}

/* Medium devices (landscape phones, 767.98px Max width) */
@media (max-width: 991.98px) {
}

/* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) and (max-width: 991.98px) {
  .step-card:not(:first-of-type):after {
    content: none;
  }
  .step-counter {
    font-size: 44px;
  }
  .text-bold {
    font-size: 12px;
  }
  .blog-card,
  .study-card {
    margin-bottom: 40px;
  }
}

/* Large devices (desktops, 992px and up) */
@media (min-width: 992px) and (max-width: 1199.98px) {
  .step-card:not(:first-of-type):after {
    left: -30%;
    width: 56%;
  }
  .step-card:nth-of-type(2):after {
    left: -11%;
    width: 39%;
  }
}

/* Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
}

