.offers-container {
  background: #fffbfa;
  padding: 100px 0;
}
.offer-card {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 150px;
}
.offer-card:nth-of-type(even) {
  flex-direction: row-reverse;
}
.offer-card:last-of-type {
  margin-bottom: 0;
}
.offer-card > div {
  width: 48%;
}
.offer-view {
  width: 50%;
}
.sub-title {
  font-size: 23px;
  color: #28294b;
  font-weight: 400;
  line-height: 1;
}

/* Extra small devices (portrait phones, less than 576px) */
@media (max-width: 575.98px) {
  .offer-card > div {
    width: 100%;
  }
  .offer-card {
    flex-direction: column-reverse;
  }
  .offer-card:nth-of-type(even) {
    flex-direction: column-reverse;
  }
  .offer-details {
    text-align: center;
  }
  .offer-view {
    margin-bottom: 20px;
  }
}

